/*------------------------------------*\
    ::Main Footer
\*------------------------------------*/
@include sticky-footer(
    $wrap: '.wrap-all-the-things',
    $row:  '.main-head, .main-torso, .main-foot',
    $flex: '.main-torso'
);
.main-foot {
    box-shadow: 0 0 20px rgba(#000, .1) inset;
    text-align: center;
    &__inner {
        padding: $gap--big $gap;
    }
    h2 {
        @extend %h6;
    }
}